<!-- 综艺详情页----wzh -->
<template>
  <div class="variety-detail">
    <div class="title">
      <h3>综艺详情</h3>
    </div>
    <detailtop ref="dl" :initData="detailData" :jsonUrl="jsonUrl" v-on:pressDown="keepDo"></detailtop>
    <div class="recommend">
      <h4 v-if="likeShow">猜你喜欢</h4>
      <ul class="recommend-content" ref="contentBox">
        <li v-for="(item,index) in detailData.assetList" :key="index" :id="('box-'+index)" :class="{'active':index===itemNo}">
          <span class="itemName">{{item.assetName}}</span>
          </li>
      </ul>
    </div>
    <div class="mask" v-if="maskShow">
      该资产已下线，点击返回体验更多精彩视频
    </div>
    <keyDo ref="keydo" v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>

<style scoped="VarietyDetail">
.mask{
  width: 1280px;
  height: 720px;
  background-color: rgba(0, 0, 0, .8);
  color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  z-index: 999;
  line-height: 1050px;
  font-size: 40px;
}
.variety-detail {
width: 1280px;
height: 720px;
background: url("../../../assets/detail/detail-bg.png");
overflow: hidden;
}

.variety-detail .title {
position: relative;
left: -20px;
display: block;
width: 254px;
height: 58px;
background: #002D50;
line-height: 58px;
font-size: 25px;
font-weight: 300;
text-align: center;
transform: skewX(-30deg)
}

.title h3 {
transform: skewX(30deg)
}

.recommend ul {
margin-left: 35px;
width: 10000px;
transition: all 0.5s;
}

.recommend h4 {
font-size: 20px;
opacity: 0.4;
line-height: 24px;
margin-left: 20px;
margin-top: 20px;
}

.recommend-content ul::after{
   content: '';
 display: block;
 clear: both;
}
.recommend-content li {
float: left;
width: 231px;
height: 130px;
margin-left: 20px;
margin-top: 20px;
background: url("../../../assets/detail/img_230x130.png") no-repeat;
background-size:100% 100%;
text-align: center;
font-size: 20px;
display: flex;
justify-content: center;
align-items: center;
}
.recommend-content li.active {
background: url("../../../assets/detail/img_230x130_active.png") no-repeat;
background-size:100% 100%;
}
.recommend-content li .itemName {
  width: 166px;
  display:-webkit-box;
  -webkit-line-clamp:3;
  -webkit-box-orient:vertical;
  /* overflow:hidden;
  text-overflow:ellipsis;
  white-space: normal; */
}
</style>

<script>
import c from '../../../js/common.js';
import detailtop from '../detail-top.vue';
import {
  HTTPUtil as api
} from '../../../fetch/api.js';
import {
  mapGetters
} from 'vuex';
import {
  client as yh
} from '../../../js/client.js'
import {bi} from '../../../js/logApi.js'
export default {
  data () {
    return {
      detailData: [],
      isShow: false,
      itemNo: -1,
      lastFocus: 0,
      jsonUrl: '',
      maskShow: false,
      likeShow: true,
      startTime: 0
    }
  },
  created () {
    if (this.$route.params.jsonUrl) {
      this.jsonUrl = this.$route.params.jsonUrl
      let url = this.$route.params.jsonUrl + this.behindParams;
      localStorage.setItem('detailJsonUrl', this.jsonUrl);
      this.getDetailData(url);
    } else if (this.$route.query.jsonUrl) {
      c.startTime();
      bi.start('0201')
      this.jsonUrl = `${this.$route.query.jsonUrl}&p=${this.$route.query.p}&k=${this.$route.query.k}&v=${this.$route.query.v}&assetId=${this.$route.query.assetId}&c=${this.$route.query.c}`;
      let url = this.jsonUrl + this.behindParams;
      localStorage.setItem('detailJsonUrl', this.jsonUrl);
      this.getDetailData(url);
    } else {
      let url = localStorage.getItem('detailJsonUrl') + this.behindParams;
      this.jsonUrl = localStorage.getItem('detailJsonUrl');
      this.getDetailData(url);
    }
  },
  mounted () {
    this.startTime = new Date() - 0;
  },
  computed: {
    ...mapGetters(['behindParams', 'navpos'])
  },
  components: {
    detailtop
  },
  methods: {
    getDetailData (url) {
          if (!url) {
            // let type = this.$route.params.bp.type;
            // let route =  this.$router;
            this.maskShow = true;
            setTimeout(function(){
              // c.routerBack(this.$router, undefined)
                       window.location.href = './../esport/jx/index'

            },1500)
          }
      console.log('获取详情页信息');
      api.jsonp(url, (res) => {
        if (res.data == null){
          this.maskShow = true;
          let down = JSON.parse(localStorage.getItem('historyListsr'));
          if (down != null) {
            let deleteUrl = this.$route.params.jsonUrl;
            down.forEach((item, index) => {
              if (item.jsonUrl.indexOf(deleteUrl) != -1) {
                yh.delHistory(index)
              }
            });
          }

          // let id = new URLSearchParams( this.$route.params.jsonUrl.split('?')[1]).get('assetId');
          let id = this.GetRequest(this.$route.params.jsonUrl.split('?')[1]).assetId;
          let urlPath = 'http://112.18.251.100:7077/uds/cloud/collection/del?version=1'
          let data = {
            siteId: yh.siteId,
            userId: yh.userId,
            collectType: 1, // 收藏类型(0-主播,1-资产,2-专题)
            relateId: id
          }
          api.post(urlPath, data, (res) => {
            console.log(res);
            if (res.mes === 'ok') { console.log('删除收藏成功'); }
          })
          return
        } else {
          this.maskShow = false;
        }
        this.detailData = res.data;
        this.newJump();
        if (this.detailData.assetList.length == 0){
          this.likeShow = false;
        } else {
          this.likeShow = true;
        }
        // yh.initPlayer(60,90,480,270);
        // this.playVideo();
      });
    },

    keyCode (kc) {
      if (kc === 'down') {

      } else if (kc === 'up') {
        this.up();
      } else if (kc === 'left') {
        this.left();
      } else if (kc === 'right') {
        this.right();
      } else if (kc === 'KeyEnter') {
        this.enter();
      } else if (kc === 'KeyBack') {
        this.$store.commit('GET_ISSHOWPLAY', false);
        try {
          // yh.closePlayer()
        } catch (err) {
          console.log(err.message);
        }
        // this.setLogJump();
        if (this.$route.params.bp && this.$route.params.bp.type){
          c.routerBack(this.$router, this.$route.params.bp.type)
        } else {
          c.routerBack(this.$router, undefined)
        }
      }
    },
    GetRequest (url) {
      var theRequest = new Object();
      let strs = url.split('&');
      for (var i = 0; i < strs.length; i++) {
        theRequest[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1]);
      }
      return theRequest;
    },
    playVideo () {
      yh.showVideo(this.jsonUrl)
    },
    up () {
      this.isShow = false
      this.lastFocus = this.itemNo
      this.itemNo = -1
      this.$refs.dl.getKeyListen();
    },
    left () {
      if (this.itemNo === 0) return;
      if (this.itemNo >= 3) {
        this.$refs.contentBox.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
      }
      this.itemNo--
    },
    right () {
      if (this.itemNo === this.detailData.assetList.length - 1) return
      this.itemNo++
      if (this.itemNo >= 4) {
        this.$refs.contentBox.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
      }
    },
    enter () {
      localStorage.setItem('BI_recmd_id', 3);
      let url = this.detailData.assetList[this.itemNo].jsonUrl;
      this.jsonUrl = url;
      let layType = this.detailData.assetList[this.itemNo].layout;
      let cld = this.detailData.assetList[this.itemNo].assetId;
      this.$store.commit('GET_ISSHOWPLAY', false);
      // yh.closePlayer();
      if (layType == 'Game_Zy'){
        // 相同类型资产只刷新数据不跳转
        localStorage.setItem('detailJsonUrl', url);
        url = url + this.behindParams;
        this.getDetailData(url)
        this.$refs.contentBox.style.marginLeft = '35px';
        this.isShow = false;
        this.itemNo = -1;
        this.lastFocus = 0;
        this.$refs.dl.getKeyListen('enter');
      } else {
        this.setLogJump();
        c.routerSkip(url, '1', layType, {}, this.$router);
      }
      // let biObj = {
      //   stay_time:new Date - this.startTime,
      //   recmd_id:'3',
      //   pos_id:'',
      //   page_type:'0301',
      //   page_id:this.detailData.assetId,
      //   click_type:2,
      //   click_id:cld,
      // }
      // bi.jump(biObj);
      let recBiObj = {
        pos_id: '',
        recmd_id: '3',
        page_type: '0301',
        page_id: this.detailData.assetId,
        cid: this.detailData.assetId,
        click_type: '1'
      }
      bi.jumpRecommend(recBiObj);
    },
    newJump (){
      if (c.isJump() == 'jump'){
        c.removeJump();
        let jumpObj = {
          page_type: '0301',
          page_id: this.detailData.assetId,
          parent_page_type: c.getParentPageType(),
          parent_page_id: c.getParentPageId()
        }
        bi.newJump(jumpObj)
      }
    },
    setLogJump (){
      c.setJump();
      c.setParentPageType('0301');
      c.setParentPageId(this.detailData.assetId);
    },
    keepDo (isShow, name, position) {
      // if (this.detailData.assetList.length == 0){
      //   this.$refs.dl.getKeyListen('up');
      //   return;
      // }
      // if (position === 'down') {
      //   this.isShow = true
      //   this.itemNo = this.lastFocus
      // }
      if (this.detailData.assetList){
        if (this.detailData.assetList.length == 0){
          this.$refs.dl.getKeyListen('up');
          this.isShow = false
          return;
        }
      }
      if (position === 'down') {
        this.isShow = true
        this.itemNo = this.lastFocus
      }
    }
  }
}
</script>



